<template>
  <div class="login-page">
    <div v-show="showManulLogin">
      <van-field v-model="email" placeholder="Input your email" />
      <van-field
        v-model="password"
        :type="showPass ? 'text' : 'password'"
        placeholder="input password"
        :right-icon="showPass ? 'eye-o' : 'closed-eye'"
        @click-right-icon="showPass = !showPass"
      />
      <div class="login-tips">Forget password</div>
      <div class="login-button-wrap">
        <van-button @click="loginByEmail" class="login-btn" color="#6B7DFC"
          >Sign up/ Login</van-button
        >
      </div>
    </div>
    <div class="login-tools" v-show="showManulLogin">
      <div class="login-tool">
        <div class="login-tool-item" @click="loginByGoogle">
          <van-icon
            size="47"
            :name="require('../assets/icon/google-icon.png')"
          />
        </div>
        <div class="login-tool-item" @click="loginByFacebook">
          <van-icon
            size="47"
            :name="require('../assets/icon/facebook-icon.png')"
          />
        </div>
        <div class="login-tool-item" @click="loginByAnonymous">
          <van-icon
            size="36"
            :name="require('../assets/icon/anonymous.png')"
          />
        </div>
        <div v-if="false" class="login-tool-item" @click="toPhoneLogin">
          <van-icon
            size="47"
            :name="require('../assets/icon/phone-icon.png')"
          />
        </div>
      </div>
      <div class="tools-tips">
        By signing up, you confirm that you agree to our Terms of Use and have
        read and understood our Privacy Policy
      </div>
    </div>
    <!--  -->
    <div class="login-loading" v-show="!showManulLogin">
      <div class="loader">
        <van-loading size="100px" vertical>Loading...</van-loading>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { Field, Button, Loading } from "vant";
import firebase from "firebase/app";
import { mapActions } from "vuex";
import * as Server from "../server";

Vue.use(Field)
  .use(Button)
  .use(Loading);
export default {
  name: "login",
  data() {
    return {
      showManulLogin: false,
      email: "",
      password: "",
      showPass: false
    };
  },
  created() {
    console.log("Region => ", firebase.auth().languageCode);

    firebase.auth().onAuthStateChanged(async user => {
      console.log("Firebase User => ", user);
    });

    if (this.showLoginPage()) {
      this.showManulLogin = true;
      return;
    }

    this.showManulLogin = false;
    this.loginByAnonymous();

    // setTimeout(() => {
    //   window.location.reload();
    // }, 15 * 1000);
  },
  methods: {
    ...mapActions(["login", "logout"]),
    showLoginPage(){
      let url = window.location.href;
      let trackInfo = JSON.parse(localStorage.getItem("trackInfo") || "{}");
      let result = (/inTest/gi.test(url)  || /manul/gi.test(url)) || ( trackInfo.appId !== 305 );
      return result;
    },
    async tracking(){
      let profile = JSON.parse(localStorage.getItem("profile") || "{}");
      let trackInfo = JSON.parse(localStorage.getItem("trackInfo") || "{}");
      if(!profile.uid || !trackInfo.source){
        console.log("no trackinfo => ",profile, trackInfo);
        return;
      }
      let {status, data} = await Server.TrackSourceFrom(profile.uid ,trackInfo.source, trackInfo.refby);
      console.log("Tracking: ", status, " => ", data);
    },
    loginByAnonymous() {
      firebase
        .auth()
        .signInAnonymously()
        .then(async res => {
          console.log("signInAnonymously", res.user);
          let user = res.user;
          // let type = this.showManulLogin ? "google" : "anonymous";
          // await this.login({ user, method: { type: type, id: "" } });
          await this.login({ user, method: { type: "anonymous", id: "" }  });
          this.$router.push("/home");
          await this.tracking();
        }).catch(error => {
          this.$toast(error.message);
        });
    },
    loginByEmail() {
      if (!this.email) {
        this.$toast("Please input email");
        return;
      }
      if (!this.password) {
        this.$toast("Please input password");
        return;
      }
      firebase
        .auth()
        .signInWithEmailAndPassword(this.email, this.password)
        .then(async res => {
          const { user } = res;
          await this.login({ user, method: { type: "email", id: this.email } });
          this.$router.push("/home");
          await this.tracking();
        })
        .catch(error => {
          console.log(error);
          if (error.code === "auth/user-not-found") {
            this.createdUserByEmail();
          } else {
            this.$toast(error.message);
          }
        });
    },
    createdUserByEmail() {
      firebase
        .auth()
        .createUserWithEmailAndPassword(this.email, this.password)
        .then(res => {
          console.log("createUserWithEmailAndPassword => ", res);
          this.loginByEmail();
        })
        .catch(error => {
          console.log(error);
          this.$toast(error.message);
        });
    },
    loginByGoogle() {
      console.log("Login by google");
      const provider = new firebase.auth.GoogleAuthProvider();
      firebase
        .auth()
        .signInWithPopup(provider)
        .then(async res => {
          console.log(res);
          const { user } = res;
          await this.login({ user, method: { type: "google", id: "" } });
          this.$router.push("/home");
          await this.tracking();
        })
        .catch(error => {
          this.$toast(error.message);
        });
    },
    loginByFacebook() {
      const provider = new firebase.auth.FacebookAuthProvider();
      firebase
        .auth()
        .signInWithPopup(provider)
        .then(async res => {
          console.log(res);
          const { user } = res;
          await this.login({ user, method: { type: "facebook", id: "" } });
          this.$router.push("/home");
          await this.tracking();
        })
        .catch(error => {
          this.$toast(error.message);
        });
    },
    toPhoneLogin() {
      this.$router.push("/phone-login");
    }
  }
};
</script>

<style lang="less">
@keyframes loaderAnimal {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.login-loading {
  background: #fff;
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  .loader {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }
}
.login-page {
  padding: 80px 35px 0;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  .login-tips {
    text-align: right;
    font-size: 12px;
    color: #cfcfcf;
    padding: 10px 0;
  }
  .login-button-wrap {
    margin-top: 40px;
    text-align: center;
    // padding: 20px;
    .login-btn {
      height: 36px;
      line-height: 1;
      font-size: 18px;
      font-weight: bold;
      padding: 0 45px;
    }
  }
  .login-tools {
    text-align: center;
    padding: 60px 0;

    .login-tool {
      display: flex;
      align-items: center;
      justify-content: center;
      &-item {
        background: #ddd;
        width: 47px;
        height: 47px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;

        & + .login-tool-item {
          margin-left: 15px;
        }
      }
    }

    .tools-tips {
      font-size: 12px;
      color: #cfcfcf;
      line-height: 16px;
      margin-top: 15px;
    }
  }
}
</style>
